

.active  {
    background-color: 	#4682B4;
}
.App {
    width: 500px;
    border: 5px solid #4169E1;
    background-color: #4169E1;
    padding-top: 20px;
    text-align: center;
    border-radius: 3px;
}
.App > h1 {
    text-align: center;
    margin-bottom: 20px;
    background-color: #4169E1;
    color: #fff;
}
.App > ul {
    width: 500px;
    margin: 0 auto;
    

}
.App > ul > li {
    width: 113px;
    height: 33px;
    display: inline-block;
    list-style: none;
    text-align: center;
    border: 1px solid #fff;
    padding: 5px;
    font-weight: bolder;
    background-color: 	#6495ED;
    
}
.App > ul > li:hover {
    background-color: 	#4682B4;

}
.App > ul > li > a {
    width: 123px;   
    height: 32px;
    display: inline-block; 
    position: relative;
    left: -5px;
    top: -5px;
    padding-top: 10px;
    
}
.App > ul > li > a:hover {
    background-color: 	#4682B4;
    
}
.Second {
    border: 2px solid #fff;
}
.Second > ul {
    position: relative;
    left: -1px;
}
.Second> ul> li{
    width: 483px;
    list-style: none;    
    border: 2px solid #fff;
    padding: 10px 5px;
    font-size: 0.8em;
    background-color: #87CEFA;

}
.Second> ul> li h2 {
    font-weight: bold;
}
.Detail {
    width: 499px;
    position: relative;
    left: -2px;
    background-color: #B0E0E6;
    text-align: center; 
    padding-bottom: 30px;
   
    
}
.imageDiv {
    width: 480px;
    text-align: center;
}
.imageDiv img {
    width: 200px;
    margin: 20px 0;
}
.imageDiv h2 {
   margin-bottom: 5px;
}
.imageDiv p {
    margin-bottom: 30px;
    color: #444;
}
.content {
    width: 400px;
    line-height: 2;
    background-color: #E6E6FA;
    margin: 0 auto;
    padding: 10px;
    border: 2px solid #483D8B;
    border-radius: 3px;
}
